@import url('./reset.css');
@import url('https://fonts.googleapis.com/css2?family=Mada:wght@300;400;500&display=swap');

.heading {
    font-size: 4.5rem;
    font-family: Mada;
    text-shadow: 4px 4px 5px #0005;
}

.subtitle {
    font-size: 1rem;
    font-family: Mada;
    margin-bottom: 1rem;
}

.addTaskBtn {
    background-color: #4caf50;
    margin-left: 0.6rem;
    margin-bottom: 2rem;
    padding: 0.3rem;
    transition: background-color 0.3s ease;
}

.addTaskBtn:hover {
    background-color: #398c3c;
}

.inputTask {
    padding: 0.5rem; 
    font-size: 1rem;
    border: 1px solid #ccc; 
    border-radius: 0.3rem; 
}

.inputTask:focus {
    outline: none; 
    border-color: #4caf50;
}

@media only screen and (max-width: 900px) {
    .addTaskBtn {
        margin-bottom: 0;
    }
}

@media only screen and (max-width: 600px) {
    .heading {
        font-size: 3rem;
    }
    .subtitle {
        display: none;
    }
}



